<template>
  <div class="app-login">
    <!-- 导航 -->
    <header class="mint-header">
      <div class="mint-header-button is-left">
        <a href="#/Index" class="router-link-active">
          <span class="mint-button-icon">
            <i class="mintui mintui-back"></i>
          </span>
        </a>
      </div> 
    </header>
    <!-- 登录页面 -->
    <div class="title">
      <h1>淘淘客</h1>
    </div>
    <form action="#" class="from">
      <div class="shuru">
        <input type="text" placeholder="请输入用户名" v-model="uname">
        <input type="password" placeholder="请输入密码" v-model="upwd">
      </div>
      <div class="zhuce">
        <router-link to="Register">免费注册</router-link>
      </div>
      <input class="denglu" type="button" value="登 录" @click="btnLogin">
    </form>
  </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
  data(){
    return{
      uname:"", //双向绑定用户名
      upwd:"" //双向绑定密码
    }
  },
  methods:{
    btnLogin(){
      //功能:获取用户输入用户名和密码
      //验证如果通过发送ajax请求给服务器
      //程序并且获得返回结果
      //获取用户名密码
      var u=this.uname;
      var p=this.upwd;
      console.log("2:"+u+":"+p);
      //创建正则表达式
      var ureg=/^[a-z0-9_]{3,16}$/;
      if(!ureg.test(u)){
        //如果验证失败,提示错误信息
        //console.log("3:验证失败");
        Toast("用户名错误,请检查");
        return;
      }
      //密码验证
      var preg=/^[0-9]{3,8}$/;
      if(!preg.test(p)){
        Toast("密码错误");
        return;
      }
      console.log(1);
      //发送ajax请求
      var url="http://127.0.0.1:3000";
      url+="/login?uname="+u+"&upwd="+p;
      this.axios.get(url).then(result=>{
        console.log(result);
        console.log(1);
        //console.log("服务器返回结果");
        if(result.data.code==1){
          Toast("登录成功");
          this.$router.push("/Index");
        }else{
          Toast("用户名或密码错误");
        }
      })
    }
  }
}
</script>
<style scoped>
.app-login{
  position: absolute;
  height: 100%;
  width: 100%;
  top:0;
  left: 0;
  overflow-y: auto;
  background-color: #fff;
}
.mint-header{
  background: transparent;
}
.mintui{
  font-size: 20px;
  font-weight: bold;
  color: rgb(26, 125, 246);
}
.title{
  width: 100%;
  display: flex;
  margin-top:20%;
  margin-bottom: 10%;
  text-align: center;
}
.title h1{
  width: 100%;
  font-family:华文琥珀;
  font-size: 50px;
  background: linear-gradient(90deg, rgb(110, 255, 238),rgb(26, 125, 246));
  background: -webkit-linear-gradient(left,rgb(110, 255, 238),rgb(26, 125, 246));
  -webkit-background-clip: text;
  color: transparent;
}
.from{
  padding:0 10%;
}
.shuru input{
  border: transparent !important;
  border-bottom: 1px solid #6eb2ff !important;
  margin-bottom: 25px !important;
}
.zhuce{
  display: flex;
  justify-content:flex-end;
  font-size: 0.8rem;
}
.zhuce a{
  color: #555 !important;
}
.denglu{
    width: 100%;
    height: 50px;
    margin-top: 10%;
    border-radius:50px !important;
    font-size: 20px !important;
    text-align: center;
    border: 0;
    background: -webkit-linear-gradient(left,rgb(26, 125, 246),rgb(110, 255, 238)) no-repeat;
    color: #fff !important;
    box-shadow: 0 0.08rem 0.16rem #f7c7b1;
}
</style>



