			<div class="xinxi_1">
				<img class="xinxi_img" src="../img/125.jpg">
			</div>
      <div class="xinxi_2">
				<h3>{{uname}}</h3>
			</div>
    </div>

		<!-- 分栏 -->
		<div class="mui-content">
		  <ul class="mui-table-view mui-grid-view mui-grid-9">
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <img src="../img/银行卡.png" alt="">
		        <div class="mui-media-body">待付款</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <img src="../img/货车.png" alt="">
		        <div class="mui-media-body">待收货</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <img src="../img/退款关怀.png" alt="">
		        <div class="mui-media-body">退换/售后</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <img src="../img/账单.png" alt="">
		        <div class="mui-media-body">全部订单</div>
					</a>
				</li>
		  </ul> 
		</div>

		<!-- 收藏 -->
		<div class="mui-content">
		  <ul class="mui-table-view mui-grid-view mui-grid-9">
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <h4>1</h4>
		        <div class="mui-media-body">优惠券</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <h4>15</h4>
		        <div class="mui-media-body">商品收藏</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
					<a href="#">
		        <h4>3</h4>
		        <div class="mui-media-body">店铺收藏</div>
					</a>
				</li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
