			<div class="mui-card">
				<div class="mui-card-header mui-card-media card_img">
					<div class="card_text">
						<p>复仇者联盟4:终局之战</p>
						<p>最新活动:复联4火爆上映,影票限时优惠...</p>
					</div>
				</div>	
