				<span class="mui-tab-label">个人中心</span>
			</a>
		</nav>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return{
			uname:''//当前用户的用户名
		}  
	},
	created(){
    this.use()
  },
	methods :{
		Mine(){
			this.$router.push("/Mine")
		},
		Shopcart(){
			this.$router.push("/Shopcart")
		},
		index(){
			this.$router.push("/Index")
		},
		jump(){
			this.$router.push("/Classify")
		},
		/*判定是否登录 */
		use(){
			var url="use";
			this.axios.get(url).then(result=>{
				if(result.data.code==-1){
					MessageBox('您还没有登录,已为您跳转至登录页');
					this.$router.push("/login");
				}else{
					console.log(result.data);
					this.uname=result.data.data[0].uname;
				}
			})
		},
	},
}
</script>
<style scoped>
* { touch-action: pan-y; }
body{
  background: #ccc !important;
}

.xinxi{
	width: 100%;
	height: 13rem;
	line-height: 15rem;
	background: -webkit-linear-gradient(left,#00C2AB,#3E94FF);
  background: linear-gradient(90deg, #00C2AB,#3E94FF);
}
.xinxi_1{
	width: 100%;
	height: 70%;
	text-align: center;
}
.xinxi_img{
	width: 7rem;
	height: 7rem;
	border-radius: 50%;
	margin-top: 2rem;
}
.xinxi_2{
	width: 100%;
	height: 30%;
	text-align: center;
}
.xinxi_2 h3{
	color: aliceblue;
	margin-top: 1rem;
}

