.mui-content ul{
	background: #fff;
}
.mui-content>ul>li{
	width: 25%;
	padding: 0px!important;
	border-bottom: 0!important;
}
.mui-content>ul>li>a{
	padding: 5px!important;
}
.mui-content>ul>li>a>img{
	width:40px;
	height:40px;
}
.mui-content>ul>li>a>h4{
	color: #000;
	margin: 10px 0px !important;
}
.mui-media-body{
	color: #555 !important;
	margin-top: 0px !important;
	font-size:12px !important;
}

/*卡片*/
.daohang{
	margin-top: 15px;
}
.card_img{
	height:40vw;
	background-image: url(../img/card.jpg);
	background-size:cover;
	/* display: flex !important; */
	/* flex-direction:column-reverse !important; */
}
.card_text{
	overflow: hidden;
	line-height: 17px;
	bottom: 0px;
	left: 0px;
	margin-bottom: 10px;
	margin-left: 10px;
	position: absolute;
}
.card_text p{
	margin: 0;
	color: #fff;
}
/*底部导航栏*/
.mui-bar{
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
} 
</style>



