<template>
  <div class="app-register">
    <!-- 导航 -->
    <header class="mint-header">
      <div class="mint-header-button is-left">
        <a href="#/Login" class="router-link-active">
          <span class="mint-button-icon"><i class="mintui mintui-back"></i></span>
        </a>
      </div> 
    </header>

    <!-- 注册页面 -->
    <div class="title">
      <h1>用户注册</h1>
    </div>
    <form action="#" class="from">
      <div class="shuru">
        <input type="text" name="uname" v-model="uname" placeholder="请输入用户名">
        <input type="password" name="upwd" v-model="upwd" placeholder="请输入密码">
        <input type="text" name="phone" v-model="phone" placeholder="请输入手机号">
      </div>
      <input class="denglu" type="button" value="注 册" @click="btn" size="large">
    </form>
  </div>
</template>
<script>
import {Toast} from 'mint-ui'
import {MessageBox} from 'mint-ui'
export default {
    data(){
        return{
            uname:'',
            upwd:'',
            phone:''
        }
    },
    methods:{
        btn(){
            var uname=this.uname;
            console.log()
            var upwd=this.upwd;
            var phone=this.phone;
            var reg1=/^.{6,15}$/;
            if(!reg1.test(upwd)){
                Toast("您的密码格式不正确");
                return;
            }
            var reg2=/^((\+86|0086)\s+)?1[3-8]\d{9}$/;
            if(!reg2.test(phone)){
                Toast("您的手机格式不正确");
                return;
            }
            var postData=this.qs.stringify({
                uname:uname,
                upwd:upwd,
                phone:phone
            })
            console.log(postData);
            var url="zhuce";
            this.axios.post(url,postData).then(result=>{
                console.log(result.data.code);
                if(result.data.code==1){
                MessageBox('注册成功', '自动跳转登录页面');
                this.localhost="/Login";
                this.$router.push("/login");
                }
            })
        }
    }
}
</script>
<style scoped>
.app-register{
  position: absolute;
  height: 100%;
  width: 100%;
  top:0;
  left: 0;
  overflow-y: auto;
  background-color: #fff;
}
.mint-header{
  background: transparent;
}
.mintui{
  font-size: 20px;
  font-weight: bold;
  color: rgb(26, 125, 246);
}
.title{
  width: 100%;
  display: flex;
  margin-top:20%;
  margin-bottom: 10%;
  text-align: center;
}
.title h1{
  width: 100%;
  font-family:华文琥珀;
  font-size: 50px;
  background: linear-gradient(90deg, rgb(110, 255, 238),rgb(26, 125, 246));
  background: -webkit-linear-gradient(left,rgb(110, 255, 238),rgb(26, 125, 246));
  -webkit-background-clip: text;
  color: transparent;
}
.from{
  padding:0 10%;
}
.shuru input{
  border: transparent !important;
  border-bottom: 1px solid #6eb2ff !important;
  margin-bottom: 25px !important;
}

.denglu{
    width: 100%;
    height: 50px;
    margin-top: 10%;
    border-radius:50px !important;
    font-size: 20px !important;
    text-align: center;
    border: 0;
    background: -webkit-linear-gradient(left,rgb(26, 125, 246),rgb(110, 255, 238)) no-repeat;
    color: #fff !important;
    box-shadow: 0 0.08rem 0.16rem #f7c7b1;
}
</style>



