<template>
  <div class="app-details"> 
    <!-- 顶部导航栏 -->
    <header id="header-main" class="mui-bar mui-bar-transparent">
			<a @click="goOff" class="mui-icon mui-icon-left-nav mui-pull-left dhspan" id="header-span"></a>
      <a @click="Shopcart" class="mui-icon mui-icon-extra mui-icon-extra-cart mui-pull-right dhspan" id="header-span1"></a>
      <mt-navbar class="header-a" id="header-a" v-model="selected">
        <mt-tab-item href="#SP" id="1">商品</mt-tab-item>
        <mt-tab-item href="#PJ" id="2">评价</mt-tab-item>
        <mt-tab-item href="#XQ" id="3">详情</mt-tab-item>
      </mt-navbar>
		</header>

    <!-- 轮播图 -->
    <div class="mui-slider" id="SP">
      <div class="mui-slider-group">
        <div class="mui-slider-item"><img :src="xqlinfo[0].tjimg"/></div>
        <div class="mui-slider-item"><img :src="xqlinfo[0].tjimg"/></div>
        <div class="mui-slider-item"><img :src="xqlinfo[0].tjimg"/></div>
        <div class="mui-slider-item"><img :src="xqlinfo[0].tjimg"/></div>
      </div>
      <div class="mui-slider-indicator">
				<div class="mui-indicator mui-active"></div>
				<div class="mui-indicator"></div>
				<div class="mui-indicator"></div>
				<div class="mui-indicator"></div>
			</div>
    </div>

    <!-- 商品信息 -->
    <div class="shangpin">
      <div class="xinxi">
        <span class="fuhao">¥</span>
        <span class="jiaqian">{{xqlinfo[0].tjprice}}</span>
        <span class="cuxiao">促销价</span>
      </div>
      <!-- <div class="xinxi1">
        <label>价格:</label>
        <del>¥119</del>
      </div> -->
      <div class="xinxi2">
        {{xqlinfo[0].tjtitle}}
      </div>
      <div class="xinxi3">
        <span class="xinxi3_span1">快递: 0.00</span>
        <span class="xinxi3_span2">月销量 {{xqlinfo[0].tjpeople}}件</span>
        <span class="xinxi3_span3">河南郑州</span>
      </div>
    </div>
    <!-- 选择规格栏 -->
    <div>
      <ul class="mui-table-view">
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						<span class="cuxiao1">促销</span>
						<span class="cuxiao2">积分</span>
						<span class="cuxiao3">购买可得30积分</span>
					</a>
				</li>
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						<span class="cuxiao1">服务</span>
            <ul class="fuwu">
              <li>正品保证</li>
              <li>急速退款</li>
              <li>七天退换</li>
            </ul>
					</a>
				</li>
			</ul>
      <ul class="mui-table-view">
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						<span class="cuxiao1">选择</span>
            <span class="cuxiao3">请选择颜色分类/尺码</span>
					</a>
				</li>
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						<span class="cuxiao1">参数</span>
            <span class="cuxiao3">品牌 面料主材质...</span>
					</a>
				</li>
			</ul>
    </div>

    <!-- 评论分割线 -->
    <div class="fgx" id="PJ">
      <div class="fgx-xian"><hr></div>
      <div class="fgx-text">评价</div>
      <div class="fgx-xian"><hr></div>
    </div>

    <!-- 评论 -->
    <div class="pinglun">	
      <ul class="mui-table-view" style="margin:0;">
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						<span class="pinglun-span1">商品评价 (125)</span>
            <span class="pinglun-span2">查看全部</span>
					</a>
				</li>
        <li>
          <ul class="pinglun-ul">
            <li>质量好(99)</li>
            <li>很划算(20)</li>
            <li>物流慢(6)</li>
          </ul>
        </li>
			</ul>
      <div class="pl-nr">
        <div class="pl-name">
          <img src="../img/125.jpg">
          <span>ttk***long</span>
        </div>
        <div class="pl-text">
          颜色很正，客服推荐尺码标准，丝绸的?这个价位能买到，很是超值，亲肤柔软，减龄洋气，睡觉穿也不会难受，拿回来就让我?洗了，也没起球，感觉还不错，价格我觉得非常实?惠，之前买过一件差不多这个价格的，然后质量特别差，色差也大，线头多，这次是真的满意.没有色差，值得购买，滑滑的很舒服，下水洗了不变形不掉色，不抽丝，挺好的，做工我觉得近乎完美了吧，款式和设计都很时尚。
        </div>
        <div class="pl-time">
          2019-05-07 颜色分类:LZ141-2香槟色【主图款】;尺码:XL：165CM-173CM 体重：120斤-140斤
        </div>
      </div>
      <div class="pl-nr">
        <div class="pl-name">
          <img src="../img/125.jpg">
          <span>ttk***long</span>
        </div>
        <div class="pl-text">
          颜色很正，客服推荐尺码标准，丝绸的?这个价位能买到，很是超值，亲肤柔软，减龄洋气，睡觉穿也不会难受，拿回来就让我?洗了，也没起球，感觉还不错，价格我觉得非常实?惠，之前买过一件差不多这个价格的，然后质量特别差，色差也大，线头多，这次是真的满意.没有色差，值得购买，滑滑的很舒服，下水洗了不变形不掉色，不抽丝，挺好的，做工我觉得近乎完美了吧，款式和设计都很时尚。
        </div>
        <div class="pl-time">
          2019-05-07 颜色分类:LZ141-2香槟色【主图款】;尺码:XL：165CM-173CM 体重：120斤-140斤
        </div>
      </div>
    </div>

    <!-- 详情分割线 -->
    <div class="fgx" id="XQ">
      <div class="fgx-xian"><hr></div>
      <div class="fgx-text">详情</div>
      <div class="fgx-xian"><hr></div>
    </div>

    <!-- 详情 -->
    <h3 class="xq-h3">商品图片</h3>
    <div class="xiangqing">
      <img src="../img/xqimg1.jpg" alt="">
      <img src="../img/xqimg2.jpg" alt="">
      <img src="../img/xqimg3.jpg" alt="">
      <img src="../img/xqimg4.jpg" alt="">
      <img src="../img/xqimg5.jpg" alt="">
    </div>

    <!-- 底部导航 -->
    <nav class="mui-bar mui-bar-tab">
        <a class="mui-tab-item" >
          <span class="mui-icon mui-icon-extra mui-icon-extra-custom"></span>
          <span class="mui-tab-label" >客服</span>
        </a>
        <a class="mui-tab-item">
          <span class="mui-icon mui-icon-extra mui-icon-extra-heart"></span>
          <span class="mui-tab-label">收藏</span>
        </a>
        <a class="mui-tab-item">
          <span class="mui-icon mui-icon-extra mui-icon-extra-share"></span>
          <span class="mui-tab-label">分享</span>
        </a>
        <a class="mui-tab-item db-jrgwc" @click="addcart">
          加入购物车
        </a>
        <a class="mui-tab-item db-ljgm">
          立即购买
        </a>  
		</nav>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui'
import {Toast} from "mint-ui"
import $ from 'jquery'
export default {
  data(){
    return{
      selected:'1',//选项卡
      tjid:this.$route.query.tjid, //获取tjid
      xqlinfo:[{
        tjimg:'',tjprice:'',tjtitle:'',tjpeople:''
      }],//显示点击商品信息
      
    }
  },
  created(){
    this.xqlist();
  },
  methods:{
    goOff(){
      this.$router.go(-1);
    },
    Shopcart(){
      this.$router.push("/Shopcart")
    },
    // 点击加入购物车
    addcart(){
      // console.log(1111);
      // 获取当前加入购物车的商品相关信息
      var tjid=this.tjid;
      var tjtitle=this.xqlinfo[0].tjtitle;
      var tjprice=this.xqlinfo[0].tjprice;
      var tjimg=this.xqlinfo[0].tjimg;
      // 发送ajax
      var url="addcart?tjid="+tjid+"&tjtitle="+tjtitle+"&tjprice="+tjprice+"&tjimg="+tjimg;
      // 获取返回结果
      // console.log(url)
      this.axios.get(url).then(result=>{
        // console.log(result.data.code);
        if(result.data.code==1){
            Toast("加入成功,请前往购物车支付")
            //详见main.js
            //点击显示购物车的数量相加
              // this.$store.commit("increment");
        }
      })
    },
    //加载点击当前商品的信息
    xqlist(){
      console.log(this.tjid)
      var url="xiangqing?tjid="+this.tjid;
      this.axios.get(url).then(result=>{
        console.log(result.data.data)
      this.xqlinfo=result.data.data;
      })
    }
  },
   mounted(){
      this.$nextTick(()=>{
        window.onscroll = function() {
         var $headerMain = $("#header-main");
         var $headerSpan = $("#header-span");
         var $headerSpan1 = $("#header-span1");
         var $headerul = $("#header-a");
          if(window.pageYOffset >= 90){
           $headerMain.addClass('search-bg');
           $headerSpan.addClass('dhspan-bg');
           $headerSpan1.addClass('dhspan-bg');
           $headerul.addClass('header-a-1');
          } else {
           $headerMain.removeClass('search-bg');
           $headerSpan.removeClass('dhspan-bg');
           $headerSpan1.removeClass('dhspan-bg');
           $headerul.removeClass('header-a-1');
          }
        }
      })
    },
 
}
</script>
<style scoped>
* { touch-action: pan-y; }
/*顶部栏*/
.mui-bar{
  width: 100%;
}
.search-bg{
  background: #fff !important;
}
.dhspan{
  background: rgba(0, 0, 0, 0.5);
  padding: 0 !important;
  padding: 5px !important; 
  margin: 5px 0;
  border-radius: 25px;
  color: #fff;
}
 
.dhspan-bg{
  color:  rgba(0, 0, 0, 0.5);
  background: transparent;
}
.mui-slider-item{
  font-size: 30px !important;
}
.header-a{
  opacity: 0;
  height: 100%; 
  padding:0px 8%;
  margin: 0;
}
.header-a-1{
  opacity: 1;
}
.mint-navbar .mint-tab-item {
  padding: 13px 0;
  color: #333;
}
.mint-navbar .mint-tab-item.is-selected{
    color: red;
    border-bottom:2px solid  red;
    margin-bottom: -1px;
}

/* 商品信息 */
.shangpin{
  background-color: #FFF;
  padding: 0 10px;
  margin-bottom: 10px;
}
.xinxi{
  color: #FF0036;
  height: 36px;
  line-height: 36px;
}
.fuhao{
  font-family: arial;
  font-style: normal;
  font-size: 18px;
  position: relative;
  top: 2px;
}
.jiaqian{
  font-size: 24px;
  position: relative;
  top: 2px;
  margin-right: 5px;
}
.cuxiao{
  background-color: #fb6878;
  color: #FFF;
  font-size: 12px;
  padding: 1px 2px;
}
/* .xinxi1{
  font-size: 12px;
  height: 20px;
  line-height: 20px;
  color: #888;
  display: inline-block;
  font-family: '\5fae\8f6f\96c5\9ed1';
} */
.xinxi2{
  font-size: 15px;
  color: #051B28;
  line-height: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  max-height: 63px;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  overflow: hidden;
  word-break: break-all;
  padding-top: 3px;
}
.xinxi3{
  display: flex;
  background-color:#fff;
  line-height: 30px;
  font-size: 12px;
  color: #999;
}
.xinxi3_span1{
  text-align: left;
}
.xinxi3_span2{
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
}
.xinxi3_span3{
  text-align: right;
}

/* 规格栏 */
.mui-table-view{
  margin-bottom: 10px;
}
.mui-table-view:before{
  height:0;
}
.mui-table-view-cell:after{
  height: 0;
}
.mui-table-view:after{
  height: 0;
}
.cuxiao1{
    float: left;
    font-size: 13px;
    width: 30px;
    height: auto;
    color: #999;
    overflow: hidden;
    margin-right: 5px;
}
.cuxiao2{
  float: left;
  font-size: 11px;
  width: 30px;
  height: auto;
  color: #FF0036;
  overflow: hidden;
}
.cuxiao3{
  float: left;
  font-size: 12px;
  height: auto;
  color: #000;
  overflow: hidden;
}
.fuwu{
  position: relative;
  padding:0px;
  color: #333;
  list-style-type: disc;
  list-style-position:inside;
  float: left;
}
.fuwu li{
  padding-right: 10px;
  display: inline;
  font-size: 12px;
  color: #000;
  overflow: hidden;
}

/* 评论 */
.pinglun{
  margin-bottom: 10px;
}
.pinglun-span1{
  font-size: 14px;
  line-height: 20px;
  color: #666;
}
.pinglun-span2{
  float: right;
  font-size: 14px;
  line-height: 20px;
  -webkit-appearance: none;
  text-align: center;
  color: #FF0036;
  margin-right: 20px;
}
.pinglun-ul{
    list-style-type: none;
    padding: 0 10px;
    margin: 0;
    max-height: 38px;
    overflow: hidden;
}
.pinglun-ul li{
  float: left;
  background-color: #FEE;
  color: #666;
  margin: 6px 3px;
  padding: 6px 9px;
  text-align: center;
  border-radius: 26px;
  -moz-border-radius: 26px;
  font-size: 12px;
  line-height: 14px;
}
.pl-nr{
  padding: 4px 10px;
  background: #fff;
}
.pl-name{
  line-height: 16px;
  font-size: 12px;
  margin-bottom: 10px;
  position: relative;
  display: flex;
}
.pl-name img{
  width: 24px;
  height: 24px;
  border-radius: 20px;
}
.pl-name span{
  vertical-align:middle;
  padding: 0 5px;
  line-height: 22px;
  color: #999;
}
.pl-text{
  font-size: 13px;
  line-height: 16px;
  text-align: justify;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  height: 32px;
  overflow: hidden;
  word-break: break-all;
}
.pl-time{
  font-size: 10px;
  padding-top: 8px;
  color: #999;
  line-height: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 分割线 */
.fgx{
  padding: 0 20%;
  text-align: center;
  color: #999;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.fgx-xian{
  -webkit-box-flex: 1;
  flex: 1;
  width: 0;
  flex-basis: 0;
  max-width: 100%;
  display: block;
  padding: 0 !important;
  position: relative;
}
.fgx-text{
  padding: 0 10px;
  font-size: 15px;
}

/* 详情 */
.xq-h3{
  background: #FFF;
  font-weight: 400;
  height: 40px;
  line-height: 40px;
  position: relative;
  padding-left:12px;
  font-size: 16px;
  color: #666;
  border-left: 3px solid #FF0036;
  margin: 0px;
}
.xiangqing img{
  width: 100%;
  vertical-align: top;
}

/* 底部栏 */
.mui-bar{
  box-shadow: 0 0 0;
}
.db-jrgwc{
  width: 2% !important;
  color: #FFF !important;
  background: #ff8800;
}
.db-ljgm{
  width: 2% !important;
  color: #FFF !important;
  background: red;
}

</style>


