    <!-- 秒杀 -->
    <!-- <div class="miaosha">
      <div class="jishi">
          <strong class="jishi_title">淘淘秒杀</strong>
          <strong class="jishi_chang">0点场</strong>
          <span class="jishi_time" @load="load" id="spanTime"></span>
          <div class="jishi_duo">更多秒杀...</div>
      </div>
    </div> -->

    <!-- 热卖 -->
    <div class="home_remai1" @click="click">
      <div class="home_remai2" v-for="(item,i) of remai" :key="item.id">
        <div class="home_remai3">
          <h4 :class="remaiH[i]">{{item.rmname}}</h4>
