              <img :src="item.mrimg">
            </div>
          </router-link>
        </div>
      </div>
    </div>

    <!-- 展览 -->
    <div class="home_zhanlan" v-for="item of zhanlan" :key="item.id" @click="click">
      <div class="zhanlan_neirong">
        <div class="zhanlan_img">
          <div class="zhanlan_img1">
            <router-link to="item.zlurl">
              <img :src="item.zlimg" alt="">  
            </router-link>
          </div>
          <div class="zhanlan_img2">
