<!--2.添加script-->
<script>
import $ from 'jquery'
  export default{
    data(){
      return {
      lunboList:[],//轮播图数组
      fenzu:[],//分组数组
      remai:[],//热卖数组
      remaiH:["text1","text2","text3","text4"],//热卖名称样式循环
      meiri:[],//每日数组
      meiriH:["meiri_strong1","meiri_strong2","meiri_strong3","meiri_strong4","meiri_strong5","meiri_strong6","meiri_strong7","meiri_strong8"],//每日名称样式循环
      zhanlan:[],//展览数组
      tuijian:[],//推荐数组
      list:[],
      pno:0   //当前的页数
      }
    },
    created(){
      //vue组件创建成功后调用
      this.loadImgList();//主页轮播
      this.fenzuList();//主页分组
      this.remaiList();//主页热卖
      this.meiriList();//主页每日
      this.zhanlanList();//主页展览
      this.tuijianList();//主页推荐
      // this.load();//秒杀倒计时
    },
    methods:{
      click(){
        this.$router.push("/Commodity")
      },
      Mine(){
        this.$router.push("/Mine")
      },
      Shopcart(){
        this.$router.push("/Shopcart")
      },
      index(){
        this.$router.push("/Index")
      },
      jump(){
        this.$router.push("/Classify")
      },
      //主页轮播
      loadImgList(){
        var url="lunbo";
        this.axios.get(url).then(result=>{
        this.lunboList=result.data;
        })
      },
      //主页分组
      fenzuList(){
        var url="fenzu";
        this.axios.get(url).then(result=>{
        this.fenzu=result.data;
        })
      },
      //主页热卖
      remaiList(){
        var url="remai";
        this.axios.get(url).then(result=>{
        this.remai=result.data;
        })
      },
      //主页每日
      meiriList(){
        var url="meiri";
        this.axios.get(url).then(result=>{
        this.meiri=result.data;
        })
      },
      //主页展览
      zhanlanList(){
        var url="zhanlan";
        this.axios.get(url).then(result=>{
        this.zhanlan=result.data;
        })
      },
      //主页推荐
      tuijianList(){
      if(this.hasMore==false){
        this.$toast("没有更多的数据了...");
        return;
      }
      this.pno++;
      var url="tuijian?pno="+this.pno;
      this.axios.get(url).then(result=>{
        // console.log(result.data.data)
        var rows= this.tuijian.concat(result.data.data);
        this.tuijian=rows;
        // console.log(this.tuijian)
        if(this.pno >= result.data.pageCount){
            this.hasMore = false;
        }
      })
    },
      //跳转到详情页
      jumpInfo(e){
          var tjid=e.target.dataset.tjid;
          console.log(tjid); 
          this.$router.push("/Details?tjid="+tjid);
      },
    },
    //顶部导航栏滑动变色
    mounted(){
      this.$nextTick(()=>{
        window.onscroll = function() {
         //var headerMain = document.getElementById('header-main');
         var $headerMain=$("#header-main");
          if(window.pageYOffset >= 90){
           //headerMain.classList.add('search-bg');
           $headerMain.addClass("search-bg");
          } else {
          $headerMain.removeClass('search-bg');
          }
        }
      })
    },
    
  }
</script>

