<template>
  <div class="app-container">
    <!-- 顶部导航 -->
    <header class="mui-bar mui-bar-nav">
			<a @click="goOff" class="mui-icon mui-icon-left-nav mui-pull-left"></a>
			<h1 class="mui-title">购物车</h1>
		</header>

    <!-- 购物车 -->
    <div class="app-shopcart">
      <div class="mui-card">
        <div class="mui-card-header">
          <div>
            全选&nbsp;<input type="checkbox"   @change="selectAll" :checked="allcb"/> 
          </div>
          <input type="button" value="删除选定" @click="removeSelect"/>
        </div>
        <div class="mui-card-content">
          <div class="mui-card-content-inner">
            <ul class="mui-table-view">
              <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
                <div class="mui-media-body gwc_div">
                  <div class="gwc_div1">
                    <!--1:复选框-->
                    <span><input type="checkbox" :checked="item.cb" @change="modifyItem" :data-i="i"/></span>
                    <!-- 商品图片 -->
                    <img class="mui-media-object mui-pull-left" :src="item.limg">
                  </div>
                  <div class="gwc_div2">
                      <!--2:商品名称 -->
                    <div>{{item.lname}}</div>
                    <!--3:商品价格-->
                    <span>¥{{item.price}}</span>
                  </div>
                  <div class="gwc_div3">
                    <!--4:删除按钮-->
                    <input type="button" value="×" @click="removeItem" :data-id="item.id"/>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <!-- 结算 -->
    <div class="jiesuan">
      <div class="jiesuan_qx">
        <input type="checkbox"  @change="selectAll" :checked="allcb"/>&nbsp;全选
      </div>
      <div class="jiesuan_zj">
        <span>总计:</span>
        <span>¥{{total.toFixed(2)}}</span>
      </div>
      <div class="jiesuan_js">
        <router-link to>结算</router-link>
      </div>
    </div>

    <!-- 底部导航 -->
      <nav class="mui-bar mui-bar-tab">
       	<a class="mui-tab-item mui-active" to="index" @click="index">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label" >首页</span>
			</a>
      <a class="mui-tab-item mui-active" @click="jump">
				<span class="mui-icon mui-icon-list"></span>
				<span class="mui-tab-label" >分类</span>
			</a>
			<a class="mui-tab-item mui-active" to="Shopcart" @click="Shopcart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
      <a class="mui-tab-item mui-active"  @click="Mine">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">个人中心</span>
			</a>
      </nav>

  </div>
</template>
<script>
import {MessageBox} from 'mint-ui'
  export default {
    data(){
      return {
        // list:[],    //保存购物列表
        allcb:true,//全选状态 默认未选中
        id:this.$route.query.id,
        shoplist:[],//加入购物车的列表
      }
    },
    created() {
      this.loadMore();
      this.use();
    },
    methods:{
      goOff(){
      this.$router.go(-1);
      },
      Mine(){
        this.$router.push("/Mine")
      },
      Shopcart(){
        this.$router.push("/Shopcart")
      },
      index(){
        this.$router.push("/Index")
      },
      jump(){
        this.$router.push("/Classify")
      },
      modifyItem(e){
        var cb = e.target.checked;
        var i = e.target.dataset.i;
        this.shoplist[i].cb = cb;
        var count = 0;
        for(var item of this.shoplist){
            if(item.cb){
              count++;
            }
        }
        if(count==this.shoplist.length){
          this.allcb = true
        }else{
          this.allcb = false;
        }
      },
      removeSelect(){
        this.$messagebox.confirm("是否删除选中商品?")
       .then(action=>{
         var ids = "";
         for(var item of this.shoplist){
             if(item.cb){
               ids += item.id+",";
             }
         }
         if(ids.length==0){
           this.$toast("请选中您需要删除的商品");
           return;
         }
         ids =  ids.substring(0,ids.length-1);
         var url = "http://127.0.0.1:3000";
         url+="/delM?ids="+ids;
         this.axios.get(url).then(result=>{
           this.$toast(result.data.msg);
           this.loadMore();
          })
        }).catch(result=>{
          console.log("取消")
        });
      },
      selectAll(e){
        var cb = e.target.checked;
        for(var item of this.shoplist){
          item.cb = cb;
        }
        this.allcb = cb;
      },
      removeItem(e){
        this.$messagebox.confirm("是否删除指定商品?")
        .then(action=>{
        var id = e.target.dataset.id;
        var url = "http://127.0.0.1:3000";
        url+="/removeCartItem?id="+id;
        this.axios.get(url).then(result=>{
          if(result.data.code==1){
             this.$toast("删除成功");
             this.loadMore();
          }else{
             this.$toast("删除失败");
          }
          })
        })
        .catch(result=>{
        })
      },
      loadMore(){
        var url = "http://127.0.0.1:3000";
        url+="/getCart";
        this.axios.get(url).then(result=>{
          var rows = result.data.data;
          for(var item of rows){
             item.cb = true;
          }    
          this.shoplist = rows;
        })
      },
      /*判定是否登录 */
      use(){
        var url="use";
        this.axios.get(url).then(result=>{
          if(result.data.code==-1){
            MessageBox('您还没有登录,已为您跳转至登录页');
            this.$router.push("/login");
          }else{
            // console.log(result.data);
            this.uname=result.data.data[0].uname;
          }
        })
      },
    },
    //计算总价
    computed:{
      total(){
        var sum=0;
        for(var p of this.shoplist){
            if(this.allcb){
            sum+=p.price;
            }
        }
        return sum;
      }
    },
    
  }
  
</script>
<style scoped>
* { touch-action: pan-y; }
.app-container{
  padding-top: 40px;
}

/*购物车*/
.mui-card-content-inner{
    position: relative;
    padding: 0px;
}
.gwc_div{
  display: flex;
  align-items:center;
  justify-content:space-between;
}
.gwc_div1{
  display: flex;
  align-items:center;
  justify-content:space-between;
}
.gwc_div1 img{
  max-width:60px;
  height:60px;
  margin-left: 10px;
}
.gwc_div2{
  width: 75%;
}
.gwc_div2 div{
  height: 44px;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-word;
}
.gwc_div2 span{
  color: red;
  font-size: 17px;
  margin-right: 5px;
}
.gwc_div2 input{
  font-size: 17px;
  padding: 0;
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

/*结算栏*/
.jiesuan{
  position: fixed;
  bottom: 50px;
  width: 100%;
  height: 50px;
  background: #fff;
  border-top: .02667rem solid #e7e7e7;
  display: flex;
  border-bottom:1px solid rgba(0, 0, 0, 0.2);
}
.jiesuan_qx{
  width: 25%;
  justify-content: center;
  align-items: center;
  display: flex;
}
.jiesuan_qx input{
  width: 18px;
  height: 18px;
}
.jiesuan_zj{
  width: 50%;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}
.jiesuan_zj :first-child{
  font-size: 14px;
  margin-right: 7px;
  font-weight: bold;
}
.jiesuan_zj :last-child{
  color: #f50;
  font-size: 19px;
}
.jiesuan_js{
  width: 25%;
  margin-left:10px;
  background: #f50;
  justify-content: center;
  align-items: center;
  display: flex;
}
.jiesuan_js a{
  color: #fff;
}

/*底部导航栏*/
.mui-bar{
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
} 
</style>



