<template>
  <div class="app-classify">
    <!-- 顶部导航栏 -->
    <header class="mui-bar mui-bar-nav">
			<a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
			<h1 class="mui-title">商品分类</h1>
		</header>

    <!-- 商品分类 -->
    <div class="mui-content mui-row mui-fullscreen">
			<div class="mui-col-xs-3" style="border-right:1px solid #c8c7cc;">
				<div id="segmentedControls" class="mui-segmented-control mui-segmented-control-inverted mui-segmented-control-vertical"><a class="mui-control-item mui-active" href="#content1">热门推荐</a><a class="mui-control-item" href="#content2">手机数码</a><a class="mui-control-item" href="#content3">家用电器</a><a class="mui-control-item" href="#content4">电脑办公</a><a class="mui-control-item" href="#content5">美妆护肤</a><a class="mui-control-item" href="#content6">个护清洁</a><a class="mui-control-item" href="#content7">汽车生活</a><a class="mui-control-item" href="#content8">男装</a><a class="mui-control-item" href="#content9">女装</a><a class="mui-control-item" href="#content10">母婴童装</a><a class="mui-control-item" href="#content11">图书音像</a><a class="mui-control-item" href="#content12">运动户外</a><a class="mui-control-item" href="#content13">食品生鲜</a><a class="mui-control-item" href="#content14">酒水饮料</a><a class="mui-control-item" href="#content15">生活用品</a></div>
			</div>
			<div id="segmentedControlContents" class="mui-col-xs-9">
        <!-- 热门推荐 -->
        <div id="content1" class="mui-control-content mui-active">
          <img style="width:100%;" src="../img/新人礼包.png" alt="">
  
          <h4>热门分类</h4>
          <div class="mui-content">
            <ul class="mui-table-view mui-grid-view mui-grid-9">
              <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3 mui-li" v-for="item of remen" :key="item.id">
                <a @click="shop">
                  <img  :src="item.rmimg">
                  <div class="mui-media-body">{{item.rmtitle}}</div>
                </a>
              </li>
            </ul> 
          </div>

          <h4>分类排名</h4>
          <div class="mui-content">
            <ul class="mui-table-view mui-grid-view mui-grid-9">
              <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3 mui-li">
                <a href="#">
                  <img src="../img/70.jpg" alt="">
                  <div class="mui-media-body">手机</div>
                </a>
              </li>
            </ul> 
          </div>
        </div>    
      </div>
	  </div>
   
   <!-- 底部导航 -->
    <nav class="mui-bar mui-bar-tab">
			<a class="mui-tab-item mui-active"  @click="index">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label" >首页</span>
			</a>
      <a class="mui-tab-item mui-active" @click="jump">
				<span class="mui-icon mui-icon-list"></span>
				<span class="mui-tab-label" >分类</span>
			</a>
			<a class="mui-tab-item mui-active"  @click="Shopcart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
      <a class="mui-tab-item mui-active"  @click="Mine">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">个人中心</span>
			</a>
		</nav>
  </div>
</template>
<script>
export default {
  data(){
    return{
      remen:[],//分类-热门
    }
  },
  created(){
    //vue组件创建成功后调用
    this.remenList();//热门
  },
  methods:{
    click (){
      this.$router.push("/Index")
    },
    Mine(){
      this.$router.push("/Mine")
    },
    Shopcart(){
      this.$router.push("/Shopcart")
    },
    index(){
      this.$router.push("/Index")
    },
    jump(){
      this.$router.push("/Classify")
    },
    shop(){
      this.$router.push("/Commodity")
    },
    /*热门*/
    remenList(){
        var url="remen";
        this.axios.get(url).then(result=>{
        this.remen=result.data;
        })
      },
  }
}
</script>
<style scoped>
* { touch-action: pan-y; }

.mui-segmented-control.mui-segmented-control-inverted.mui-segmented-control-vertical .mui-control-item {
    border-bottom: 1px solid #c8c7cc;
    width:100px;
}

.mui-col-xs-9{
  width: 75%;
  position: absolute;
  height: 100%;
  top:0;
  left:25%;
  overflow-y: auto;
  background-color: #fff;
  padding: 50px 7px 50px 7px;
}
.mui-control-item{
  width:100% !important;
}
h4{
  font-size: 14px;
  color:#333;
  background: #fff;
  margin-top: 10px;
}
.mui-li{
  padding: 0 !important;
  border:0px !important;
}
.mui-li a{
  padding: 0 !important;
}
.mui-li div{
  margin-top: 0px !important;
  height: 30px !important;
}
.mui-li a>img{
  width: 70px;
} 
.mui-grid-view{
  background-color: #fff !important;
  border: 0px !important;
}

/*底部导航栏*/
.mui-bar{
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
} 
</style>



